import * as React from 'react'

const ItemList = (props: any) => {
  const ListProps = {
    ...props
  }
  const { listItems } = ListProps
  
  if(!listItems || listItems.length < 0 ) {
      return 'Item is nothing'
    } {
      return (
        listItems.map(({
          id,
          name,
          desctiption,
          like_count,
          cot_count,
          price,
          is_sold_out,
          shipping_fee,
          image,
          category_id
        }, index: number) => {
          return (
            <div key={index}>
              <div>{id}</div>
              <div>{name}</div>
              <div>{desctiption}</div>
              <div>{like_count}</div>
              <div>{cot_count}</div>
              <div>{price}</div>
              <div>{is_sold_out}</div>
              <div>{shipping_fee}</div>
              <div>{image}</div>
              <div>{category_id}</div>
            </div>
          )
        })
      )
    }
}

export default ItemList